import * as React from 'react';
import { assign } from '../jsUtils';

export const BASE_SIZE = 100;
export const CELL_SIZE = BASE_SIZE;

export interface RenderSettings {
    cellSize: number
    boardWidth: number;
    boardHeight: number;
    boardFlipped: boolean;
}

const CELL_ZINDEX = 0;

function cell(rs:RenderSettings, x: number, y: number) {
    let cellClass = ((x+y) % 2 === 0) ? 'whitecell' : "blackcell";
    // if (x > 1 && x < 4 && y > 1 && y < 4) {
    //     cellClass = ((x+y) % 2 === 0) ? 'superpowerwhitecell' : "superpowerblackcell";
    // } else if (x > 0 && x < 5 && y > 0 && y < 5) {
    //     cellClass = ((x+y) % 2 === 0) ? 'powerwhitecell' : "powerblackcell";
    // }
    if (rs.boardFlipped) {
        x = (rs.boardWidth - 1 - x);
        y = (rs.boardHeight - 1 - y);
    }
    let background:any = {};
    // /*(x >= 2 && x <= 3) &&*/ (/*y==0 ||*/ y == 5) ? {
    //     backgroundImage: 'url("images/temple_CC0.png")',
    //     backgroundSize: 'cover'
    // } : {};
    return <div className={cellClass} key={x + ', ' + y}
        style={assign({
                position: 'absolute',
                pointerEvents: 'none',
                zIndex: CELL_ZINDEX,
                width: asPix(rs.cellSize), height: asPix(rs.cellSize),
                transform: `translate3d(${x*rs.cellSize}px,${y*rs.cellSize}px,0)`}, 
            background) as any}/>;
}

function asPix(n) {
    return `${n}px`;
}

export const Board = ({rs, x=0, y=0}) => {
    let cells:JSX.Element[] = [];
    for (let y = 0; y < rs.boardHeight; y++) {
        for (let x = 0; x < rs.boardWidth; x++) {
            cells.push(cell(rs, x, y));
        }
    }
    let divW = rs.cellSize * rs.boardWidth, divH = rs.cellSize * rs.boardHeight;
    let style = {
        pointerEvents: 'none',
        position: "absolute", width: asPix(divW), height: asPix(divH),
        transform: `translate(-50%, -50%) translate3d(${x}px,${y}px,${0}px)`
    };
    return <div style={style as any}>{cells}</div>;
};


