function hasHover(card, cardUIStates, zone) {
    return (cardUIStates[card.uuid] || {}).hoverZone == zone;
}

function playerStateToHandZoneProps(dispatch, { uiState: { povPlayerId }, handlers, player }): ZoneProps {
    let handCards = getHandCards(player);
    return handCards.map(cardSpriteProp);
    // Helpers:
    function cardSpriteProp(card: GameCard, index: number) {
        // let x = OFFSET_X + unit.x * CELL_SIZE, y = OFFSET_Y + unit.y * CELL_SIZE;
        const { x, y, z } = handFan(index, handCards.length);
        const zone = "hand";
        const actionHandler: ActionHandler | undefined = handlers.uuidToHandler[card.uuid];

        return {
            displayConfig: makeDisplayConfig({
                zone,
                x, y, povPlayerId,
                rotationY: povPlayerId === player.id ? 0 : 180, // || openInformation ? 0 : 180,
                rotationX: povPlayerId === player.id ? 0 : 180,
                width: HAND_CARD_WIDTH * 1.2, height: HAND_CARD_HEIGHT * 1.2,
                zIndex: z,
                // hasActionHighlight: isActionSource(card.uuid, legalActions),
                // TODO figure out why this goes out sometimes.
                hasActionHighlight: !!actionHandler,
                onDragStart: () => { dispatch(actions.setDragTarget(card.uuid)) },
                onDragEnd: () => { dispatch(actions.setDragTarget(undefined)); onClick() },
                onClick
            }),
            // type hack
            cardData: card as (GameCard & GameUnit)
        };
        async function onClick() {
            if (!actionHandler || (!isMobileDevice() && actionHandler.hasSubactions)) {
                return;
            }
            for (const action of actionHandler.actions) {
                playActionSound(action);
                await action.execute();
                await gameStateActions.syncState(dispatch);
                UNDO_STACK.push(async () => {
                    await (action as any).undo();
                    await gameStateActions.syncState(dispatch);
                });
            }
        }
    }
    function handFan(i, n) {
        let angleStart = Math.PI / 4, angleEnd = Math.PI * 3 / 4;

        let angle = interpolate(angleStart, angleEnd, i / Math.max(n - 1, 1));
        let width = n * HAND_CARD_WIDTH / 2, height = HAND_CARD_HEIGHT / 7;
        return {
            x: -Math.cos(angle) * width - HAND_CARD_WIDTH / 2,
            y: (povPlayerId === player.id ? -1 : 1) * (Math.sin(angle) * height - CELL_SIZE * 4.5) - HAND_CARD_HEIGHT / 2 * 1.2,
            z: i
        }
    }
}
// END BOARD UI CONFIG



