import * as gameStateActions from './gameStateActions'
import { hasProperty } from '../../jsUtils';
import { GameState, GameAction, GameActionNode } from '../../game-logic/engine';

export interface GlobalGameState {
    gameState: GameState;
    legalActions: GameActionNode[];
}

let initialState = {state: undefined, legalActions: []};

export function gameStateReducer(state=initialState, action) {
    if (hasProperty(gameStateActions.ACTION_FUNCS, action.type)) {
        return gameStateActions.ACTION_FUNCS[action.type](state, ...action.args);
    }
    return state;
}



