import { GameState, GameUnit, GamePlayer, GameCard } from "./engine";

export function getGameUnits(gameState: GameState): GameUnit[] {
    return gameState.board.filter(s => s.unit).map(s => s.unit as GameUnit);
}

export function getHandCards(player: GamePlayer): GameCard[] {
    return player.hand;
}

export function getDeckCards(player: GamePlayer): GameCard[] {
    return player.deck;
}

export function getGraveyardCards(player: GamePlayer): GameCard[] {
    return player.graveyard;
}

export function getGamePlayers(gameState: GameState) {
    return [gameState.player1, gameState.player2];
}


