import * as React from "react";
import { Motion, spring } from "react-motion";
import {Tooltip} from 'react-tippy';
import { CardSpriteProps, DisplayConfig } from "../sprites/BaseCardSprite";
import { Dispatch } from "../reducers/uiStateReducer";
import { assign } from "../../jsUtils";
// import {DraggableCore} from 'react-draggable';
// import {BoardCardSprite} from "./game-react/BoardCardSprite";

export const a = 1;
const getStyle = (x, y, width, height, zIndex=0) => ({
    transform: `translate3d(${x}px, ${y}px, 0)`,
    position: "absolute",
    width: `${width}px`,
    height: `${height}px`,
    zIndex
});

const springConfig = {
    stiffness: 540,
    damping: 50,
    precision: 0.0001
};

const springConfig3 = {
    stiffness: 300,
    damping: 50,
    precision: 0.0001
};


const springConfig2 = {
    stiffness: 40,
    damping: 5,
    precision: 0.0001
};

export class CardContainer extends React.Component {
    props: {
        dispatch: Dispatch,
        SpriteClass: any,
        spriteProps: CardSpriteProps
    };

    render() {
        const {dispatch, SpriteClass, spriteProps} = this.props;
        const {x, y, rotationX, rotationY} = spriteProps.displayConfig;

        return motionContainer();
        // Helpers
        function motionContainer() {
            return <Motion defaultStyle={{x: 0, y: 0, rotationX: 0, rotationY: 0}} style={{
                x: spring(x, springConfig),
                y: spring(y, springConfig),
                rotationX: spring(rotationX, springConfig3),
                rotationY: spring(rotationY, springConfig)
            }}>
                {sprite}
            </Motion>;
        }
        function sprite(interpolatedConfig) {
            let currentConfig = spriteProps.displayConfig;
            return <SpriteClass 
                cardData={spriteProps.cardData} 
                displayConfig={assign(spriteProps.displayConfig, interpolatedConfig)}/>;
        }
    }
}


