import * as React from 'react';
import { startGameWithMission } from './Missions';
import { makeDisplayConfig } from '../game-react/sprites/BaseCardSprite';
import { HandCardSprite } from '../game-react/sprites/HandCardSprite';

const styles: any = {
    container: {
        width: 200,
        height: 200,
        backgroundColor: '#77EB11',
        display: 'table',
    },
    paragraph: {
        textAlign: 'center',
        width: '100%',
        height: '100%',
        display: 'table-cell',
        verticalAlign: 'middle'
    },
};

function BossDeckView({x, y, bossCardData, bossDeck}) {
    return <>
        <HandCardSprite
            displayConfig={makeDisplayConfig({
                povPlayerId: 0, 
                x: x + 300, 
                y: y - 300,
                width: 300, 
                height: 400, 
                zone: 'screen'
            })}
            cardData={bossCardData}
        />
        {bossDeck.map((data, i) => <div><HandCardSprite 
            displayConfig={makeDisplayConfig({
                povPlayerId: 0, 
                x: 290 * (i % 4) + x, 
                y: Math.floor(i / 4) * 400 + y + 125, 
                width: 300, 
                height: 400,
                zone: 'screen'
            })}
            cardData={data}
        /></div>)}
    </>;
}

export function DeckSelectionScreen({ pickDeck, lordCardData, bossCardData, bossDeck }) {
    const rows = lordCardData.length / 4 + 1;
    return <>
        {lordCardData.map((data, i) => <div><HandCardSprite 
            displayConfig={makeDisplayConfig({
                onClick: () => pickDeck(i),
                povPlayerId: 0, 
                x: 290 * (i % 4) + 10, 
                y: Math.floor(i / 4) * 400 + 10, 
                width: 300, 
                height: 400,
                zone: 'deck-select'
            })}
            cardData={data}
        /></div>)}
        <div style={{
                    transform: `translate(100%, 0%) translate(0px, ${rows*400}px)`,
                    width: `107px`, 
                    height: `91px`, 
                    backgroundImage: "url('images/vs.png')"}}/>
        <BossDeckView x={0} y={rows * 400 + 50} bossCardData={bossCardData} bossDeck={bossDeck}/>
    </>;
}

export function MissionScreen({ startMission, missionSequence }) {
    return <>
        <p style={styles.paragraph}>
            <b>Chapter 1:</b> <br/>
            After a cave in, your party becomes stranded. Unexpected visitors emerge...  
        </p>
        {missionSequence.map(({ description }, idx) => <>
            <button key={description} type="button" className="btn" onClick={() => startMission(idx)}>
                {description}
            </button> <br />
        </>)}
    </>;
}

export function MainScreen({ clickCatArmy }) {
    return <>
        <p style={styles.paragraph}>
            Choose your team
      </p>,
        <button type="button" className="btn" onClick={clickCatArmy}>
            Cats
      </button>
    </>;
}

