import * as React from 'react';
import { GameUnit, GameCard } from '../../game-logic/engine';

// START PROPS DATA

export interface DisplayConfig {
    // Interpolated by react-motion:
    x, y: number;
    rotationX, rotationY: number;
    // Not interpolated:
    opacity: number;
    zone: string;
    zIndex: number;
    povPlayerId: number;
    width, height: number;
    isHovered: boolean;
    miscStyle: any;
    hasActionHighlight: boolean;
    onMouseLeave?: () => void;
    onMouseEnter?: () => void;
    onDragStart?: () => void;
    onDragEnd?: () => void;
    onClick?: () => void;
    onDrop?: (obj) => void;
}

export function makeDisplayConfig({
        x, y, zIndex=0, rotationX=0, rotationY=0, opacity=1, zone, width, height, povPlayerId, 
        onMouseLeave=(undefined as undefined | (() => void)), 
        onMouseEnter=(undefined as undefined | (() => void)),
        miscStyle={} as any,
        isHovered=false,
        hasActionHighlight=false,
        onDragEnd=(undefined as undefined | (() => void)),
        onDragStart=(undefined as undefined | (() => void)),
        onClick=(undefined as undefined | (() => void)),
        onDrop=undefined as any}): DisplayConfig {
    return {
        x, y, zone,
        zIndex, rotationX, rotationY,
        opacity,
        width, height, povPlayerId,
        onMouseEnter,
        miscStyle,
        hasActionHighlight,
        onMouseLeave,
        isHovered,
        onDragStart, onDragEnd,
        onClick, onDrop
    };
}

export interface CardSpriteProps {
    displayConfig: DisplayConfig;
    cardData: GameCard & GameUnit;
}

// END PROPS DATA

export var GoldBox = (props : {gold: number, className: string}) => {
    return <div className={props.className}>
        <img className="gold" src="images/coin.png" />
        <div className="manacost"> {props.gold} </div>
    </div>;
}

export var ManaBox = (props : {mana: number, className: string}) => {
    return <div className={props.className}>
        <img className="mana" src="images/blast.png" />
        <div className="manacost"> {props.mana} </div>
    </div>;
}

export var TextBox = (props: {text: string, className: string}) => {
    return <div className={props.className}>
        <div className="manacost"> {props.text} </div>
    </div>;
}

export const CostBox = (props: {gold: number, mana: number}) => (
   <div className="costbox">
       <GoldBox className="resourcecost" gold={props.gold}/>
       <ManaBox className="resourcecost" mana={props.mana}/>
   </div>);

export var ImageFill = (props: {outline?, boxShadow?, image: string, borderWidth?: string, borderColor: string, borderRadius: string, opacity?: number}) => {
   let style = {
       backgroundImage: `url("${props.image}")`,
       backgroundRepeat: 'no-repeat',
       backgroundSize: 'cover',
       outline: props.outline,
       boxShadow: props.boxShadow,
       opacity: props.opacity,
       boxSizing: "border-box",
       borderColor: props.borderColor,
       borderRadius: props.borderRadius,
       position: 'absolute',
       display: 'block',
       backgroundColor: 'black',
       borderStyle: 'solid',
       borderWidth: props.borderWidth,
       width: '100%',
       height: '100%'
   };
   return <div style={style as any}/>;
}

export var BackgroundFill = (props: {background: string, borderColor: string, borderRadius: string}) => {
    let style = {
        background: props.background,
        borderColor: props.borderColor,
        borderRadius: props.borderRadius,
        display: 'block',
        position: 'absolute',
        overflow: "auto",
        top: "0px",
        left: "0px",
        borderStyle: 'solid',
        borderWidth: '0.3mm',
        zIndex: 1,
        width: '100%',
        height: '100%'
    };
    return <div style={style as any}/>;
 }


