import { makeActions, makeDispatchActions, assign, append } from "../../jsUtils";
import { GlobalGameState } from "./gameStateReducer";
import { UIState, CardUIState } from "./uiStateReducer";

export const ACTION_FUNCS = {
    setCardHoverState(state: UIState, uuid: number, hoverZone?: string) {
        let cardUIStates = state.cardUIStates;
        let cardUIState:CardUIState = state.cardUIStates[uuid] || {};
        if (state.draggingUuid && uuid !== state.draggingUuid) {
            hoverZone = undefined;
        }
        cardUIStates = assign(cardUIStates, {
            [uuid]: assign(cardUIState as any, {hoverZone})
        });
        return assign(state, {cardUIStates});
    },
    setUIMenuFunction(state: UIState, menuFunction) {
        return assign(state, {menuFunction});
    },
    setDragTarget(state: UIState, uuid?: number) {
        return assign(state, {draggingUuid: uuid});
    },
    setActionTargetting(state: UIState, targetState) {
        let {actionState} = state;
        actionState = assign(actionState, targetState);
        return assign(state, {actionState});
    },
    assign
};

export const actions = makeActions(ACTION_FUNCS);


