function replaceAll(target, search, replacement) {
    return target.replace(new RegExp(search, 'g'), replacement);
}

export function processDescription(txt) {
    txt = txt || "";
    if (typeof txt !== "string") {
        txt = txt.join('</br>');
    }
    txt = replaceAll(txt, '{Nightwatch}', "Nightwatch. <small><i>(Ready this unit end of turn)</i></small>");
    txt = replaceAll(txt, '{Defender}', "Taunt. <small><i>(Attackers must target taunts, if possible)</i></small>");
    txt = replaceAll(txt, '{Raze}', "Raze. <small> <small><i>(Also damages opponent's VP)</i></small></small>");
    txt = replaceAll(txt, '{Passive}', "Passive. <small> <small><i>(Can't attack Core)</i></small></small>");
    txt = replaceAll(txt, '{Flight}', "Flight. <small><i>(Can attack through occupied tiles)</i></small>");
    txt = replaceAll(txt, '{Diagonal Bound}', "Diagonal Bound. <small><i>(Can only move diagonally)</i></small>");
    txt = replaceAll(txt, '{Orthogonal Bound}', "Orthogonal Bound. <small><i>(Cannot move diagonally)</i></small>");
    txt = replaceAll(txt, '{Territorial}', "Territorial. <small><i>(If an ally dies on a square this can reach, it can move there instantly)</i></small>");
    txt = replaceAll(txt, '{Magicbound}', "Magicbound. <small><i>(Unit does not Ready unless magic was spent to do so)</i></small>");
    txt = replaceAll(txt, '{Evasive}', "Evasive. <small><i>(Unit takes no damage while attacking)</i></small>");
    txt = replaceAll(txt, '{Charge}', "Charge. <small><i>(Unit has a free attack after each move action)</i></small>");
    txt = replaceAll(txt, '{Twinstrike}', "Twinstrike. <small><i>(Unit has a bonus attack after each attack action)</i></small>");
    txt = replaceAll(txt, '{Brawler}', "Brawler. <small><i>(Unit Readyes after first attack of turn)</i></small>");
    txt = replaceAll(txt, '{Noblefighter}', "Noble. <small><i>(Unit can't attack pawns)</i></small>");
    txt = replaceAll(txt, '<s3>', '<small><small><small>');
    txt = replaceAll(txt, '</s3>', '</small></small></small>');
    txt = replaceAll(txt, "{tap:}", "<b>{tap}:</b>"); 
    txt = replaceAll(txt, "{tap}", `<div class="smallresourcecost inlineblock">
     <img class="smalltap" src="images/tapsymbol.png" />
 </div>`);
    txt = replaceAll(txt, "{dice}", `<small>(<div class="smallresourcecost inlineblock">
    <img class="smalldice" src="images/dice.png" />
    </div>)</small>`);
    txt = replaceAll(txt, "{smltap}", `<div class="ssmallresourcecost inlineblock">
     <img class="ssmalltap" src="images/tapsymbol.png" />
 </div>`);
    txt = replaceAll(txt, "{untap}", `<div class="smallresourcecost inlineblock">
     <img class="smalltap" src="images/untapsymbol.png" />
 </div>`);
    for (var i=0;i<=20;i++) {
        if (txt.indexOf(`{${i}MV}`) >= 0) {
            let repl = '';
            for (let j = 0; j < i; j++) {
                repl += (`<div class="smallresourcecost inlineblock">
     <img class="smallmana" src="images/movementinline.png" />
     <div class="smallmanacost" style></div>
 </div>`);
            }
            txt = replaceAll(txt, `{${i}MV}`, repl);
        }
        if (txt.indexOf(`{${i}RNG}`) >= 0) {
            let repl = '';
            for (let j = 0; j < i; j++) {
                repl += `<div class="smallresourcecost inlineblock">
     <img class="smallmana" src="images/rangeinline.png" />
     <div class="smallmanacost" style></div>
 </div>`;
            }
            txt = replaceAll(txt, `{${i}RNG}`, repl);
        }
        if (txt.indexOf(`{${i}HP}`) >= 0) {
            let repl = '';
            for (let j = 0; j < i; j++) {
                repl += `<div class="smallresourcecost inlineblock">
     <img class="smallmana" src="images/healthinline.png" />
     <div class="smallmanacost" style></div>
 </div>`;
            }
            txt = replaceAll(txt, `{${i}HP}`, repl);
        }
        if (txt.indexOf(`{${i}MP}`) >= 0) {
            txt = replaceAll(txt, `{${i}MP}`, 
 `<div class="smallresourcecost inlineblock">
     <img class="smallmana" src="images/blast.png" />
     <div class="smallmanacost" style>${i || ""}</div>
 </div>`);
        }
        if (txt.indexOf(`{${i}GP}`) >= 0) {
            txt = replaceAll(txt, `{${i}GP}`, 
 `<div class="smallresourcecost inlineblock">
     <img class="smallmana" src="images/coin.png" />
     <div class="smallmanacost red" style>${i || ""}</div>
 </div>`);
        }
    }
    for (var i=1;i<=5;i++) {
        txt = replaceAll(txt, `{Self-defence ${i}}`, `Self-defence ${i} <small><i>(Unit has ${i} Attack during enemy turn)</i></small>`);
    }
    if (txt.toLowerCase().indexOf("{softReady}") != -1) {
        txt = replaceAll(txt, '{softReady}', "<b>soft Ready</b>");
        txt = replaceAll(txt, '{SoftReady}', "<b>Soft Ready</b>");
        txt +="</br><i><small>(<b>Soft Ready</b> - Ready, but don't allow attacking or moving)</small></i>";
    }
    return txt;
}


