import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Provider } from 'react-redux'
import { createStore } from 'redux'
import {AppContainer} from './game-react/App';
import './index.css';
import registerServiceWorker from './registerServiceWorker';
import { initGameAndDispatchState } from './game-react/reducers/gameStateActions';
import { rootReducer } from './game-react/reducers/rootReducer';
import { GameInitData, SquareContents } from './game-logic/engine';

export const GAME_STORE = createStore(rootReducer);

ReactDOM.render(
  <Provider store={GAME_STORE}>
    <AppContainer/>
  </Provider>,
  document.getElementById('root') as HTMLElement
);
registerServiceWorker();



