import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { DragSource } from 'react-dnd';
import { processDescription } from '../../game-logic/processDescription';
import { has, assign } from '../../jsUtils';
import { CardSpriteProps } from './BaseCardSprite';
import { GameCard } from '../../game-logic/engine';
import { getEmptyImage } from 'react-dnd-html5-backend';
import { isMobileDevice } from '../mobileUtils';

let orBar = (s) => (s == null ? "-" : s);
let orOne = (s) => (s == null ? 1 : s);
let getGold = (g, m, a, h, d) => {
    if (g) return g;
    if (m || h) return " ";
    if (a) return "Pay " + a;
    if (d.indexOf("(Discard 1 to play) ") > -1) {
        return "Discard 1";
    }
    return "Free";
};

let range = (n) => {
    let ret: number[] = [];
    for (var i = 0; i < n; i++) {
        ret.push(i);
    }
    return ret;
}

export var GoldBox = (props: { gold: number, className: string }) => {
    return <div className={props.className}>
        <img className="gold" src="images/coin.png" />
        <div className="manacost"> {props.gold} </div>
    </div>;
}

export var PowerSign = (props: { power: number, maxPower: number, className: string }) => {
    let powerClassName = "powervalue";
    if (props.power < props.maxPower) {
        powerClassName = "powervalue-damaged"
    }
    return <div className={props.className}>
        <img className="gold" src="images/power.png" />
        <div className={powerClassName}> {props.power} </div>
    </div>;
}

export var ManaBox = (props: { mana: number, className: string }) => {
    return <div className={props.className}>
        <img className="mana" src="images/blast.png" />
        <div className="manacost"> {props.mana} </div>
    </div>;
}

export var TextBox = (props: { text: string, className: string }) => {
    return <div className={props.className}>
        <div className="manacost"> {props.text} </div>
    </div>;
}

export const CostBox = (props: { gold: number }) => (
    <div className="costbox" style={{ transform: "scale(2)" }}>
        <GoldBox className="resourcecost" gold={props.gold} />
        {/* <ManaBox className="resourcecost" mana={props.mana}/> */}
    </div>);

export const PowerBox = (props: { power: number, maxPower: number }) => (
    <div className="powerbox" style={{ transform: "scale(2)" }}>
        <PowerSign className="resourcecost" power={props.power} maxPower={props.maxPower} />
    </div>);

export var ImageFill = (props: { className?: string, image: string, borderColor: string, borderRadius: string }) => {
    let style = {
        backgroundImage: `url("${props.image}")`,
        backgroundRepeat: 'no-repeat',
        backgroundPosition: '50% 50%',
        backgroundSize: 'cover',
        borderColor: props.borderColor,
        borderRadius: props.borderRadius
    };
    return <div className={props.className || "avatar"} style={style} />;
}

export var CardSpell = (props) => (<div className={"cardspell"} style={props.style}>
    <div className="cardspell-back" style={props.TYPE.indexOf("Equip") >= 0 ? { background: "rgb(204, 204, 220) url(\"images/spell.png\")" } : {}} />
    <div className="descriptiontext" dangerouslySetInnerHTML={{ __html: props.DESCRIPTION }}
        style={{
            borderRadius: '1mm', borderColor: "rgba(0,0,0,1.0)",
            backgroundImage: props.TYPE.indexOf("Equip") > 0 ? 'url("images/sword.png")' : 'url("images/spell-book-cut.png")',
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center',
            backgroundSize: 'contain'
        }} />
    <div className="flavourtext" dangerouslySetInnerHTML={{ __html: props.FLAVOURTEXT }} />
    {/* <div className="typebox" dangerouslySetInnerHTML={{__html: props.TYPE}}/> */}
    {/* <div className="deckflagsbox">
        {props.DECK_FLAGS.map(d => (
            <img className="statimage" src={d} />
        ))}
    </div> */}
    <ImageFill image={props.IMAGE_FILE} borderColor="rgba(0,0,0,1.0)" borderRadius="1mm" />
    <div className="namefield" style={{ borderRadius: '1mm', borderColor: "rgba(0,0,0,1.0)" }}>
        <div className="name" dangerouslySetInnerHTML={{ __html: props.NAME }} />
    </div>
    {CostBox(props)}
</div>);

function
    _cardBg({ cardData }: CardSpriteProps) {
    if (has(cardData.traits, "Spell")) {
        return 'rgb(204, 204, 220) url("images/spell.png")';
    } else if (has(cardData.traits, "Equip")) {
        return 'rgb(204, 204, 220) url("images/equip.png")';
    } else {
        return "rgb(204, 204, 220) url(\"images/spell.png\")";
    }
}

// props.TYPE.indexOf("Equip") > 0 ? 'url("images/sword.png")' : 'url("images/spell-book-cut.png")',
const _cardDescriptionBoxBg = ({ cardData }: CardSpriteProps) => {
    if (has(cardData.traits, "Spell")) {
        return "url(\"images/spell-book-cut.png\")";
    } else if (has(cardData.traits, "Equip")) {
        return "url(\"images/sword.png\")";
    } else {
        return "url(\"images/pawn.png\")";
    }
};

