import * as React from 'react';
import { AppState, Dispatch } from './reducers/rootReducer';
import { connect } from 'react-redux'
import HTML5Backend from 'react-dnd-html5-backend';
import {DragDropContext} from 'react-dnd';
import { GameUI } from './GameUI';
import { actions } from './reducers/uiStateActions';
import { MainScreen, MissionScreen, DeckSelectionScreen } from '../dashboard-react/Menu';
import { startGameWithMission } from '../dashboard-react/Missions';
import createHTML5Backend from 'react-dnd-html5-backend';
import { toInternalMissionFormat, MISSIONS } from './missions';
import { CardView } from '../dashboard-react/CardView';
import { UNDEAD_DECK, ADVENTURER_DECK, DECKS } from './decks';
import { getCardDefJson, initEngineWorker } from '../game-logic/engine';
import { anyTrue } from 'src/jsUtils';

export const GameUIContainer = (connect as any)(
    appState => ({appState}), 
    dispatch => ({dispatch}))(GameUI);


let CONTENT_LOADING = false;
@DragDropContext(HTML5Backend)
class App extends React.Component {
    props: {appState: AppState, dispatch: Dispatch};
    state: {cards, cardsByName};
    public render() {
        const {appState, dispatch} = this.props;
        const self = this;
        const menuFunction = appState.uiState.menuFunction || loadingScreen;

        if (!CONTENT_LOADING) {
            CONTENT_LOADING = true;
            (async () => {
                await initEngineWorker();
                const cards = await getCardDefJson();
                const cardsByName = {};
                for (let card of cards) {
                    cardsByName[card.name] = card;
                }
                this.setState({cards, cardsByName});
                // dispatch(actions.setUIMenuFunction(() => <CardView/>));
                dispatch(actions.setUIMenuFunction(() => missionScreen()));
            })();
        }

        // TODO combine the core into one card?
        // TODO opposing player should show what they're doing more
        // TODO "next mission" should be centred
        // TODO different colour illuminations for card types?
        return menuFunction();
        // return menuFunction();

        // function mainMenu() {
        //     return <MainScreen clickCatArmy={() => dispatch(actions.setUIMenuFunction(catMissionScreen))}/>;
        // }
        function startMission(idx) {
            dispatch(actions.setUIMenuFunction(missionDeckChoice(MISSIONS[idx], restart, next)));
            return;
            function restart() {
                startMission(idx);
            }
            function next() {
                if (MISSIONS.length > idx + 1) {
                    startMission(idx + 1);
                }
            }
        };
        function missionScreen() {
            return <MissionScreen startMission={startMission} missionSequence={MISSIONS}/>
        }
        function loadingScreen() {
            return <div>LOADING</div>;
        }
        function missionDeckChoice(mission, restart, next) {
            const decks = DECKS;
            return () => {
                const lordCardData = decks.map(d => self.state.cardsByName[d.lord]);
                const bossCardData = self.state.cardsByName[mission.bossLord];
                const bossDeck = mission.bossDeck.map(c => self.state.cardsByName[c]);
                // Sort by name, and then cost:
                bossDeck.sort((a,b) => a.name < b.name ? -1 : +1);
                bossDeck.sort((a,b) => a.cost - b.cost);
                return <DeckSelectionScreen pickDeck={pickDeck} lordCardData={lordCardData} 
                            bossCardData={bossCardData} bossDeck={bossDeck}/>;
            };
            function pickDeck(deckIdx) {
                const {lord, deck} = decks[deckIdx];
                startGameWithMission(toInternalMissionFormat(mission, lord, deck));
                dispatch(actions.setUIMenuFunction(gameState(restart, next)));
            }
        }
        function gameState(restart, next) {
            return () => {
                //style={{position: "fixed", top: "50%", left: "50%"}}
                return <div style={{transform: "translate(50%, 70%)", height: "700px"}}>
                
                    <GameUIContainer x={0} y={0} restart={restart} next={next}/>
                </div>;
            }
        }
    }
}

export const AppContainer = (connect as any)(
    appState => ({appState}), 
    dispatch => ({dispatch}))(App);



