import { GameActionNode, GameCard, GameAction, GameSquare } from "../game-logic/engine";
import { append } from "src/jsUtils";

// A functional description of action handling
export interface ActionHandler {
    // The current element to interact with
    // Either a source, or drag destination.
    // If listed, a drag source can be same as the destination, 
    // as called during endDrag.
    uuid?: number;
    card?: GameCard;
    positionIndex?: number;
    actions: GameAction[]; // Game action sequence to initiate
    // For dragging events:
    performOnDragStart?: boolean;
    performOnDragEnd?: boolean;
    hasSubactions?: boolean;
}

export interface ActionHandlers {
    uuidToHandler: { [uuid: number]: ActionHandler };
    // Maps y*10+x to an action
    positionIndexToHandler: { [uuid: number]: ActionHandler };
    cardActionHandlers: ActionHandler[];
}

/**
 * Compute action handlers, describing how legal actions map to UI gestures.
 * @param draggingUuid The currently dragging card's UUID.
 * @param actions The current legal actions, in 1-depth tree form.
 */
export function actionHandlers(draggingUuid: number, actions: GameActionNode[]) {
    const actionHandlers: ActionHandlers = {
        uuidToHandler: {},
        positionIndexToHandler: {},
        cardActionHandlers: []
    };
    // Map actions to descriptors of how the UI components should trigger them
    for (const action of actions) {
        addFromActionNode([], action);
    }
    return actionHandlers;

    function addActionHandler(handler: ActionHandler) {
        if (handler.uuid) {
            actionHandlers.uuidToHandler[handler.uuid] = handler;
        } else if (handler.card) {
            actionHandlers.cardActionHandlers.push(handler);
        } else if (handler.positionIndex !== undefined) {
            actionHandlers.positionIndexToHandler[handler.positionIndex] = handler;
        }
    }
    function addFromActionNode(actionPrefix: GameAction[], { action, subactions }: GameActionNode) {
        const { source = undefined } = action.sources[0] || {};
        const { target = undefined } = action.targets[0] || {};
        // TODO(AD) Concession for now: if a param has just source, parse it as 'target':
        const focusElem = target || source;
        // TODO(AD) Later, just check if have no source:
        const selfDraggable = !target || !source;
        const actions = append(actionPrefix, action);
        const hasSubactions = !!subactions;

        if (!focusElem || subactions && subactions.length === 0) {
            return; // Don't consider this action - leads nowhere
        }

        // Drag-and-play case:
        if (draggingUuid) {
            addDraggingHandlers();
        }
        if (focusElem.uuid && focusElem.zone === undefined) {
            addActionHandler({card: focusElem, actions, hasSubactions});
        } else if (focusElem.uuid) {
            addActionHandler({uuid: focusElem.uuid, actions, hasSubactions});
        } else {
            // Must be a square if it does not have a UUID
            const square = focusElem as GameSquare;
            addActionHandler({positionIndex: square.y * 10 + square.x, actions, hasSubactions});
        }

        function addDraggingHandlers() {
            if (focusElem.uuid === draggingUuid) {
                // Explicit to make sure we hit all the cases:
                if (subactions) {
                    for (const subaction of subactions) {
                        addFromActionNode(actions, subaction);
                    }
                } else if (selfDraggable) { // TODO selfDraggable not needed
                    // Single source self-drag
                    addActionHandler({uuid: focusElem.uuid, actions, performOnDragEnd: true, hasSubactions });
                }
            // } else if (!draggingUuid && actions.length === 1) {
            //     // Starting a drag
            //     addActionHandler({uuid: focusElem.uuid, actions, performOnDragStart: true });
            } else if (draggingUuid && actions.length == 2) {
                // Dragging onto a target, actions should be length 2
                const uuid = focusElem.uuid ? focusElem.uuid : undefined;
                const positionIndex = !focusElem.uuid ? focusElem.y * 10 + focusElem.x : undefined;
                addActionHandler({uuid, actions, positionIndex, performOnDragEnd: true, hasSubactions });
            }
        }
    }
}

export function getCardAction(handlers: ActionHandlers, uuid: number) {
    return handlers.uuidToHandler[uuid];
}

export function getSquareAction(handlers: ActionHandlers, x: number, y: number) {
    return handlers.positionIndexToHandler[y * 10 + x];
}


