import { GameAction } from "src/game-logic/engine";

const DEFAULT_SOUND = new Audio("sound/door.ogg");
const SUMMON_SOUND = new Audio("sound/ludaze.ogg");
const DEFEAT_SOUND = new Audio("sound/monster/painr.ogg");
// const VICTORY_SOUND = new Audio("sound/Jingle_Win_Synth/Jingle_Win_Synth_00.ogg");
const VICTORY_SOUND = new Audio("sound/round_end.wav");
const START_SOUND = new Audio("sound/Jingle_Win_Synth/Jingle_Win_Synth_06.ogg");
const END_TURN_SOUND = new Audio("sound/summon.ogg");
const HIT_CORE_SOUND = new Audio("sound/powerup.ogg")
const SPELL_SOUND = new Audio("sound/spell.ogg")

function play(audio) {
    audio.cloneNode(true).play();
}

export function playDefeatSound() {
    play(DEFEAT_SOUND);
}
export function playVictorySound() {
    play(VICTORY_SOUND);
}
export function playStartSound() {
    play(START_SOUND);
}


export function playActionSound(action: GameAction) {
    const Is = (s) => action.type.indexOf(s) === 0; 
    if (action.targetUnit && action.targetUnit.name === "Core") {
        play(HIT_CORE_SOUND);
    } else if (Is("PlaySpell")) {
        play(SPELL_SOUND);
    } else if (Is("PlayUnitCardOnSquare")) {
        play(SUMMON_SOUND);
    } if (Is("EndTurnAction")) {
        play(END_TURN_SOUND);
    } else {
        play(DEFAULT_SOUND);
    }
}


