function expandDeck(cards: string[]) {
    let expandedCards:string[] = [];
    for (const card of cards) {
        if (card.startsWith("x2 ")) {
            const parsedName = card.substring(3);
            expandedCards.push(parsedName);
            expandedCards.push(parsedName);
        } else {
            expandedCards.push(card);
        }
    }
    return expandedCards;
};

export const ADVENTURER_DECK = {
    lord: "Bossman Grag",
    // Notes: 
    // The Adventurer deck, defined by vanilla cards, splashy, straight-forward power manipulation.
    // Removal: Cave-in, its principle board clear, and Flamey Puff.
    deck: expandDeck([
        "x2 Burly Miner",
        "x2 Cave-in",
        "x2 Conscript",
        // "x2 Core Pieces",
        "x2 Dragon Path Kobold",
        "x2 Flamey Puff",
        "x2 Fleeting Fame",
        "x2 Larent Elite",
        "x2 Parxeer the Loyal",
        "x2 Peacat",
    ])
};

