export function toInternalMissionFormat(mission, lordCard, deck) {
    const {playerVictoryPoints, bossVictoryPoints, bossLord, bossDeck, board} = mission;
    return {
        players: [
            {
                targetVictoryPoints: playerVictoryPoints,
                lordCard,
                type: "action_ab",
                depth: 1,
                maxEvaluatedNodes: 100000,
                deck,
                actionPruneLevel: 0,
                qSearchDepth: 2
            },
            {
                targetVictoryPoints: bossVictoryPoints,
                lordCard: bossLord,
                type: "action_ab",
                depth: 1,
                maxEvaluatedNodes: 100000,
                deck: bossDeck,
                actionPruneLevel: 0,
                qSearchDepth: 2
            }
        ],
        board
    };
}


